<template>
  <div>
    <div
      v-if="item"
      id="signUpModal"
      class="modal fade"
      tabindex="-1"
      role="dialog"
      aria-labelledby="signUpTitle"
      aria-hidden="true"
      :class="'status-' + todo['status'][item.status] + ' ' + 'sort-' + todo['sort'][item.sort]"
    >
      <button
        id="signUpToggle"
        type="button"
        class="close"
        data-dismiss="modal"
        aria-label="Close"
      >
        <span aria-hidden="true">&times;</span>
      </button>
      <div class="modal-dialog" role="document">
        <form @submit.prevent="saveObj">
          <div class="modal-content">

            <div
              class="modal-header"
              :class="{'withImg' : item.page_pic}"
              :style="{'background-image': 'url(' + item.page_pic + ')'}"
            >
              <input
                id="title"
                name="title"
                type="text"
                :value="item.title"
              >
              <div
                class="pined"
              >
                <input
                  id="elevate"
                  type="hidden"
                  name="elevate"
                  :value="item.elevate"
                >
                <input
                  id="elevate-star"
                  type="checkbox"
                  @click="elevateAction"
                  :checked="item.elevate"
                >
                <label for="elevate-star">
                  <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="enable-background:new 0 0 612 792;" version="1.1" viewBox="0 0 612 792" xml:space="preserve">
                    <polygon class="st0" points="306,153 216.4,298.9 50,339 161,469.3 147.8,639.9 306,574.6 464.2,639.9 451,469.3 562,339    395.6,298.9 306,153  "/>
                  </svg>
                </label>
              </div>
            </div>

            <div class="modal-body pb-0">
              <div class="group" data-title="Status">
                <template
                  v-for="(sort, sortIndex) in todo['status']"
                >
                  <input
                    name="status"
                    type="radio"
                    :id="sort"
                    :value="sort"
                    :key="sort"
                    :checked="Number(sortIndex) === Number(item.status)"
                  >
                  <label
                    :for="sort"
                    :key="'label' + sort"
                  >{{ sort }}</label>
                </template>
              </div>
              <div class="group" data-title="Type">
                <template
                  v-for="(sort, sortIndex) in todo['sort']"
                >
                  <input
                    name="sort"
                    type="radio"
                    :id="sort"
                    :value="sort"
                    :key="sort"
                    :checked="Number(sortIndex) === Number(item.sort)"
                  >
                  <label
                    :for="sort"
                    :key="'label' + sort"
                    :class="'sort-' + sort"
                  >{{ sort }}</label>
                </template>
              </div>
              <div class="group" data-title="Deadline">
                <input
                  id="date"
                  name="date"
                  type="datetime-local"
                  placeholder="2018-01-01T00:00"
                  :value="item.deadline"
                />
              </div>
              <div class="group" data-title="File">
                <input type="file">
              </div>
              <div class="group" data-title="Note">
                <textarea
                  id="text"
                  name="text"
                  :value="item.text"
                />
              </div>

              <button
                type="cancle"
                data-dismiss="modal"
                aria-label="Close"
              >Cancle</button>

              <button type="submit">Save</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

import * as todo from '@/settings/todoSettings'

export default {
  data () {
    return {
      todo,
      item: null
    }
  },
  methods: {
    show: function (item) {
      this.item = item
      setTimeout(() => {
        $('#signUpModal').modal({
          show: true,
          backdrop: true,
          keyboard: false,
          focus: false
        })
      }, 300)
    },
    saveObj: function (submitEvent) {
      this.$parent.updateData(this.item.id, submitEvent.target.elements)
      $('#signUpModal').modal('hide')
    },
    elevateAction: function (event) {
      let st = Boolean($(event.target).is(':checked'))
      $(event.target).val(st)
      $('input[name="elevate"]').val(st)
    }
  }
}
</script>

<style lang="scss">
#signUpModal{
  $parent: &;

  max-height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  button#signUpToggle{
    font-size: 3em;
    position: absolute;
    z-index: 2;
    top: 0;
    right: .25em;
    color: white;
    line-height: 1em;
    padding: 0;
    margin: 0;
    display: inline-block;
    font-weight: normal;
    @include opacity(.8);
    text-shadow: 0 1px 0 black;
  }
  .modal-content{
    border-radius: 0;
    border: 0;
    box-shadow: 0 0 3em rgba(0,0,0,.5);
    @include rpsd{
      margin-top: 3em;
      margin-bottom: 3em;
    }
    .modal-header{
      height: 100%;
      position: relative;
      background-repeat: no-repeat;
      background-position: center !important;
      background-size: cover !important;
      border-radius: 0;
      border-top: .5em;
      border-style: solid;
      border-color: #eee;

      @include parent($parent) {
        &.sort-work .modal-header{border-color: $color1}
        &.sort-family .modal-header{border-color: $color2}
        &.sort-todo .modal-header{border-color: $color3}
      }

      .modal-title{
        color: rgb(150,150,150);
        @include breakpoint-u(default(vp, sm)) {
          font-size: 1.8em;
        }
        @include breakpoint-d(default(vp, sm)) {
          font-size: 1.3em;
        }
      }
      &.withImg{
        position: relative;
        padding: 0;
        margin: 0;
        padding-bottom: 35%;
        &:after{
          display: block;
          content: '';
          position: absolute;
          z-index: 0;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 90%;
          /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+100&0+0,0.65+97 */
          background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0.65) 97%, rgba(0,0,0,0.65) 100%); /* FF3.6-15 */
          background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 97%,rgba(0,0,0,0.65) 100%); /* Chrome10-25,Safari5.1-6 */
          background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 97%,rgba(0,0,0,0.65) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
          filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=0 ); /* IE6-9 */
        }
        .modal-title{
          position: absolute;
          z-index: 1;
          bottom: .2em;
          left: .5em;
        }
      }
    }
  }
}

.modal-content{
  $color: rgb(200,200,200);
  $colorc: rgb(64, 150, 238);

  .modal-body{
    padding-left: 0;
    padding-right: 0;
  }

  input, select, textarea{
    &,&:focus,&:active{
      outline: none;
    }
  }
  #title{
    font-size: 1.4em;
  }
  #title,textarea, input{
    display: block !important;
    width: 100%;
    padding: .4em .5em;
    color: rgb(80,80,80);
    border-width: 1px;
    border-style: solid;
    border-radius: .3rem;
    border-color: transparent;
    margin-right: .25em;
    min-height: 2.5em;
    @include transition;
      border-color: $color;
    &:hover{
    }
  }
  .pined{
    input{
      @include opacity(0)
      float: left;
      width: 0;
      height: 0;
    }
    label svg{
      cursor: pointer;
      width: 2.3rem;
      height: 2.3rem;
      stroke: $color;
      stroke-width: 50;
      fill: none;
      @include transition;
    }
    input:checked + label svg{
      fill: rgb(255, 208, 85);
      stroke: rgb(255, 208, 85);
      @include animation(pinedChecked .6s forwards);
      @include keyframes(pinedChecked) {
        33%{transform: scale(1.2) rotate(-3deg);}
        66%{transform: scale(1.2) rotate(3deg);}
      }
    }
  }
  input[type="radio"]{
    display: block;
    width: 0;
    height: 0;
    float: left;
    @include opacity(0);
    position: absolute;
    top: 0;
    left: 0;
    + label {
      display: inline-block;
      border: 1px solid $color;
      font-size: .9em;
      line-height: 1em;
      padding: .5em 1em;
      border-radius: 9em;
      text-transform: capitalize;
      @include transition;
      cursor: pointer;
      margin-bottom: .3em;
    }
    &:checked + label{
      color: white;
      border-color: $colorc;
      background: $colorc;
      &[class*='sort']{
        border-color: transparent;
      }
      &.sort-work{
        background: $color1
      }
      &.sort-family{
        background: $color2
      }
      &.sort-todo{
        background: $color3
      }
    }
  }
  textarea{
    display: block;
    width: 100%;
    height: 8em;

  }

  .group{
    position: relative;
    padding-left: 5em;
    margin-bottom: 1.25em;
    margin-left: 1.25em;
    padding-right: 1.25em;
    &:before{
      position: absolute;
      left: 0;
      top: 0;
      content: attr(data-title);
      font-weight: bold;
      font-size: .9em;
      letter-spacing: 0;
      text-align: right;
      width: 4em;
      color: rgb(100,100,100);
    }
  }

  button{
    &[type="cancle"],&[type="submit"]{
      font-size: 1.2em;
      font-weight: bold;
      letter-spacing: .05em;
      margin-top: 2em;
      display: block;
      width: 50%;
      float: left;
      padding: 1em .25em;
      cursor: pointer;
    }
    &[type="submit"]{
      background-color: $colorc;
      color: white;
    }

  }
}
</style>



