<template>
  <header class="header">
    <nav class="navbar navbar-expand navbar-dark">
      <button class="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="navbar-collapse collapse" id="navbarCollapse" style="">
        <ul class="navbar-nav mr-auto">
          <li
            class="nav-item"
            v-for="nav in navList"
            :key="nav.key"
            @click="updateSrc(nav.key)"
            :class="{'active': checkActive(nav.key)}"
            :data-filter="nav.key"
          >
            <a href="#" class="nav-link">{{ nav.title }}</a>
          </li>
          <li class="deco"></li>

        </ul>
      </div>
    </nav>
  </header>
</template>

<script>
import $ from 'jquery'

export default {
  props: ['init'],
  data () {
    return {
      navList: [
        {
          key: 'all',
          title: 'All Tasks'
        },
        {
          key: '.status-progressing',
          title: 'In Progress'
        },
        {
          key: '.status-completed',
          title: 'Completed'
        }
      ]
    }
  },
  methods: {
    updateSrc: function (st) {
      this.init.src = st
      $('#targetListItem').each(function (el) {
        $(el).removeClass('hide')
      })
      if (st !== 'all') {
        $('#targetListItem:not([class*="status-' + st + '"])').each(function (el) {
          $(el).addClass('hide')
        })
      }
    },
    checkActive: function (key) {
      if (this.init.src === null && key === 'all') {
        this.init.src = 'all'
        return true
      } else if (this.init.src === key) {
        return true
      }
    }
  }
}
</script>

<style lang="scss">
header.header{
  $fontColor: white;
  @include bkd(iphone5){
    font-size: 12px;
  }
  @include bkd(iphone6){
    font-size: 14px;
  }

  nav.navbar{
    background: default(color, uno);
    display: block;
    width: 100%;

    ul.navbar-nav{
      $parent: &;
      $parentItem: & + ' li.nav-item';

      position: relative;
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      li.nav-item{
        padding: .3em 0;
        width: 8em;
        text-align: center;
        a.nav-link{
          color: rgba(255,255,255,.8);
          cursor: pointer;
          @include parent($parentItem) {
            &.active a.nav-link{
              color: rgba(255,255,255,.9);
              font-weight: bold;
            }
          }
        }
      }

      li.deco{
        position: absolute;
        z-index: 0;
        bottom: 0;
        left: 50%;
        margin-left: #{8em / -2};
        display: block;
        width: 8em;
        height: .2em;
        @include bkd(iphone6){
          width: 33.33%;
          margin-left: -15%;
        }
        @include transition(.3s cubic-bezier(0.875, 1.650, 0.260, 0.675));
        @include parent($parent) {
          &:hover li.deco{
            height: .25em;
          }
        }
        @include parent($parentItem) {
          &:nth-child(1).active ~ li.deco{
            transform: translateX(-100%);
          }
          &:nth-child(2).active ~ li.deco{
            transform: translateX(0);
          }
          &:nth-child(3).active ~ li.deco{
            transform: translateX(100%);
          }
        }
        &:after{
          content: '';
          display: block;
          // @include bkd(md){
          //   display: none;
          // }
          width: 70%;
          height:  100%;
          margin: 0 auto;
          background: rgba(255,255,255,.5);
          @include transition;
          @include parent($parent) {
            &:hover li.deco:after{
              background: rgba(255,255,255,.65);
            }
          }
        }

      }
    }
  }
}
</style>



