<template>
  <div
    id="app"
    :style="{'background-image': 'url(' + bkg + ')'}"
  >
    <theHeader :init="init"/>
    <main class="main">
      <div class="container">
        <div
          id="mixitup-todoList"
          class="list-block row"
          :data-selector="init.src"
        >
            <baseCard
              :item="item"
              v-for="(item, itemIndex) in todoList"
              :key="itemIndex"
            />
            <div class="clear"></div>
        </div>
      </div>
      <editCard ref="editCard"/>
    </main>
    <theFooter />
  </div>
</template>

<script>
import bkg from '@/assets/images/bkg.png'

import theHeader from '@/components/theHeader'
import theFooter from '@/components/theFooter'
import baseCard from '@/components/baseCard'
import editCard from '@/components/editCard'

import * as todo from '@/settings/todoSettings.js'

import $ from 'jquery'
import mixitup from 'mixitup'

export default {
  name: 'App',
  components: {
    theHeader,
    theFooter,
    baseCard,
    editCard
  },
  data () {
    return {
      bkg,
      init: {
        src: null
      },
      todoList: todo['list']
    }
  },
  methods: {
    indexObj: function (id) {
      let index = this.todoList.findIndex(function (Obj) {
        return Obj.id === id
      })
      return index
    },
    updateStatus: function (str) {
      let target = this.todoList[this.indexObj(str)]
      let newVal = Number(target.status) + 1
      if (newVal > 2) {
        newVal = 0
      }
      target['status'] = newVal
    },
    edtiCard: function (obj) {
      this.$refs.editCard.show(obj)
    },
    updateData: function (id, obj) {
      let target = this.todoList[this.indexObj(id)]
      target['elevate'] = JSON.parse(obj.elevate.value)
      target['status'] = todo.status.indexOf(obj.status.value)
      target['title'] = obj.title.value
      target['text'] = obj.text.value
      target['sort'] = todo.sort.indexOf(obj.sort.value)
      target['deadline'] = obj.date.value
    }
  },
  updated: function () {
    var containerEl = $('#mixitup-todoList')
    mixitup(containerEl, {
      animation: {
        duration: 300
      }
    }).sort('sort:desc')
  }
}
</script>

<style lang="scss">
@import './assets/sass/main.scss';

html, body , #app{
  height: 100%;
  padding: 0;
  margin: 0;
}
#app{
  position: relative;
  width: 100%;
  height: 100%;
  display: table;
  table-layout:fixed;
  background-repeat: repeat;
  background-size: auto;
  background-attachment: fixed;

  header.header, footer.footer, main.main{
    display: table-row;
    width: 100%;
  }
  header.header, footer,footer{
    height: 1px;
  }

  main.main{
    height: auto;
    .container{
      padding-top: 1.25em;
      padding-bottom: 1.25em;
    }
  }
}
</style>



