<template>
  <div
    class="col-12 col-md-6 list-item mix"
    :class="'status-' + todo['status'][item.status] + ' ' + 'sort-' + todo['sort'][item.sort] + ' elevate-' + item.elevate"
    :data-sort="sorted(item.elevate)"
  >
    <div class="list-item-block">
      <div class="card">
        <div class="inner">
          <div class="headline-block">
            <span
              class="status"
              @click="updateStatus(item.id)"
            >
              <i class="pro"></i>
              <svg class="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52"><path class="checkmark__check" d="M14.1 27.2l7.1 7.2 16.7-16.8"/></svg>
            </span>
            <h2
              class="title"
              @click="editCard(item)"
            >{{item.title}}</h2>
            <div
              class="datetime"
              v-if="item.deadline"
            >
              <span>{{ item.deadline.split('T')[0] + ' ' + item.deadline.split('T')[1]}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as todo from '@/settings/todoSettings.js'

export default {
  props: ['item'],
  data () {
    return {
      todo
    }
  },
  methods: {
    updateStatus: function (str) {
      this.$parent.updateStatus(str)
    },
    sorted: function (str) {
      if (str === true) {
        return 1
      }
      return 0
    },
    editCard: function (obj) {
      this.$parent.edtiCard(obj)
    }
  }
}
</script>

<style lang="scss">
.list-item{
  $parent: &;

  display: block;
  height: auto;
  padding-bottom: 1.25em;
  @include transition(0);

  .card{
    position: relative;
    display: block;
    height: 100%;
    overflow:hidden;
    border: 0;
    border-radius: 1.25em;
    box-shadow: 0 0 2em rgba(0,0,0,.1);
    padding: 1.25em;
    height: auto;
    &:after{
      $sz: 100%;
      content: '';
      position: absolute;
      z-index: 0;
      left: 0;
      top: 50%;
      margin: -50% 0 0 -96%;
      display: block;
      width: $sz;
      height: 0;
      padding-bottom: $sz;
      background: rgb(150,150,150);
      border-radius: 999em;
      @include opacity(.5);
      @include transition;
    }
    @include parent($parent) {
      &.sort-work .card:after{background-color: $color1}
      &.sort-family .card:after{background-color: $color2}
      &.sort-todo .card:after{background-color: $color3}

      &:hover .card:after{
        transform: scale(1.02)
      }
      &.status-progressing .card:after{
        transform: scale(2);
        @include opacity(.2);
      }
      &.status-completed .card:after{
        @include animation(completed-animation .8s forwards);
        @include animation-timing-function(linear);
        @include keyframes(completed-animation){
          0%{
            transform: scale(2);
          }
          30%{
            @include opacity(.1);
            transform: scale(2.9);
          }
          100%{
            @include opacity(.1);
            transform: scale(3);
          }
        }
      }
      &.elevate-true .card:before{
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        top: 1em;
        right: 1em;
        display: block;
        width: .8em;
        height: .8em;
        border-radius: 999em;
        padding: 0;
        margin: 0;
        background: fade-out(default(color, uno), .5);
      }
    }
    .inner{
      position: relative;
      z-index: 9;
      padding: 1.25em;

      .headline-block{
        span.status{
          $color: rgb(180,180,180);

          position: relative;
          float: left;
          display: inline-block;
          border: 2px solid $color;
          cursor: pointer;
          width: 2em;
          height: 2em;
          @include rpsd{
            transform: scale(.8) translateY(-.3em);
          }
          &:after,&:before,i.pro{
            float: left;
            content: '';
            display: inline-block;
            width: .25em;
            height: .25em;
            margin-left: .22em;
            transform: translateY(.75em);
            background: transition;
            @include opacity(0);
            @include transition(.5s ease);
          }
          svg.checkmark{
            position: absolute;
            top: 0;
            left: 0;
            width: 50px;
            height: 50px;
            fill: none;
            stroke: rgb(122, 193, 66);
            stroke-width: 4;
            stroke-miterlimit: 10;
            transform-origin: 50% 50%;
            stroke-dasharray: 48;
            stroke-dashoffset: 48;
            @include transition;
            transform: translateX(-9px) translateY(-13px) scale(1);
          }
          @include transition(.5s ease);
          @include parent($parent) {
            &.status-progressing .inner .headline-block span.status{
              border-radius: 3em;
              &:before, &:after, i.pro{
                border-radius: 3em;
                background: $color;
                @include opacity(1);
              }
            }
            &.status-completed .inner .headline-block span.status{
              border-color: transparent;
              svg.checkmark{
                stroke-dashoffset: 7;
                stroke-dasharray: 37;
                @include transition;
              }
            }
          }
        }
        .title{
          font-family: 'aria', sans-serif;
          // font-weight: 600;
          @include rpsu{
            font-size: 1.8em;
            padding-left: 1.7em;
          }
          @include rpsd{
            font-size: 1.5em;
            padding-left: 2em;
          }
          letter-spacing: 0;
          line-height: 1.3em;
          color: rgb(150,150,150);
          display: block;
          padding-bottom: 0;
          margin-bottom: 0;
          @include m-font;
          cursor: pointer;
          &:hover{
            text-decoration: underline;
          }
        }
        .datetime{
          padding-left: 2.5rem;
          line-height: 1em;
          // margin-top: .3rem;
          @include opacity(.3);
          span{
            font-size: 12px;
            letter-spacing: 0;
            font-family: 'Arial', sans-serif !important;
            color: black;
            &:before{
              content: '';
              display: inline-block;
              width: 1.5em;
              height: 1.5em;
              background-image: url('data:image/svg+xml;base64,');
              background-size: contain;
              margin-right: .5em;
              transform: translateY(.25em);
            }
          }
        }
      }
    }
  }
}
</style>



